import { Component } from '@angular/core';
declare var $: any;

import {
  trigger,
  state,
  style,
  animate,
  transition
} from '@angular/animations';

export class Tile {
  id: number;
  name: string;
  description: string;
  details: string[];
  state: string;
}


const TILES: Tile[] = [
  {
    id: 1,
    name: 'Personals',
    description: 'JayeMeS',
    details: ['Birthday: 07/05/1990', 'Phone: 2983-501485'],
    state: 'inactive'
  },
  {
    id: 2,
    name: 'Academics',
    description: 'Chemical Engineer, UNS',
    details: ['GPA: 8.52 / 10'],
    state: 'inactive'
  },
  {
    id: 3,
    name: 'Languages',
    description: 'Spanish, English, French',
    details: ['Also basic portuguese'],
    state: 'inactive'
  },
  {
    id: 4,
    name: 'Programming',
    description: 'Java, Python, JS',
    details: ['Android Apps, WebApps with Angular'],
    state: 'inactive'
  },
  {
    id: 5,
    name: 'Work Experience',
    description: 'PLAPIQUI, Optiment, Universidad Nacional del Sur',
    details: ['Process Engineer, optimization, development of plant models, equipment design and simulation.', 'Assistant teacher'],
    state: 'inactive'
  }

];

@Component({
  selector: 'app-root',
  templateUrl: './app.component.html',
  styleUrls: ['./app.component.css']
})
export class AppComponent {

  title = 'Juan Manuel Scasso';
  tiles = TILES;


  both(index: number) {
    if (this.tiles[index].state === 'inactive') {
      this.test(index);
      this.tileClick(index);
    }
  }

  test(index: number) {
    const t = this.tiles;
    t.forEach(element => {
      element.state = 'inactive';
    });
    t[index].state = 'active';

  }

  tileClick(index: number) {
    const tileGroup = $('.main').get(0);
    let rect = tileGroup.getBoundingClientRect();
    const cornerX = rect.left;
    const cornerY = rect.top;

    $('.floating').toggleClass('floating old');

    const old: any = $('.old');
    setTimeout(() => {
      old.css('opacity', '0');
      old.css('transform', 'perspective(500px) translateY(30%)');
    }, 300);
    setTimeout(() => {
      old.remove();
    }, 800);


    rect = $('.tile').get(index).getBoundingClientRect();
    const posX = rect.left;
    const posY = rect.top;

    const containerRect = $('.main')[0].getBoundingClientRect();
    const destWidth = containerRect.right - containerRect.left;
    const destHeight = containerRect.bottom - containerRect.top;

    const clone = $('.tile').eq(index).clone();

    clone.css('left', posX);
    clone.css('top', posY);
    clone.toggleClass('floating');
    $('body').append(clone);
    clone.css('transform', 'perspective(500px) translateZ(0)');
    setTimeout(() => {
      clone.css('transform', 'perspective(500px) translateZ(100px)');
      clone.toggleClass('floater-style');
    }, 0);
    setTimeout(() => {
      clone.css('left', cornerX);
      clone.css('top', cornerY);
      clone.css('transform', 'perspective(500px) translateZ(0)');
    }, 500);
    setTimeout(() => {
      clone.css('width', '' + destWidth + 'px');
    }, 1000);
    setTimeout(() => {
      clone.css('height', '' + destHeight + 'px');
      clone.toggleClass('show-details');
    }, 1500);
  }

}




